@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.header{
  text-align: center;
  margin-bottom: 30px;
  position: relative;
  background: #000;
  color: #fff;
  font-size: 1.5rem;
  transition: 1s;
}
.header:hover{
  background: #fff;
  color: #000;
  outline: 1px solid black;
}
.header__heading{
  line-height: 100px;
  text-transform: uppercase;
}

@media(max-width: 1200px){
  .header{
    margin-bottom: 40px;
    font-size: 1.2rem;
  }
  .header__heading{
    line-height: 70px;
  }
}
@media(max-width: 900px){
  .header{
    margin-bottom: 35px;
    font-size: 1rem;
  }
  .header__heading{
    line-height: 50px;
  }
  .header:hover{
    background: #000;
    color: #fff;
    outline:none;
  }
}
@media(max-width: 700px){
  .header{
    margin-bottom: 25px;
    font-size: 14px;
  }
  .header__heading{
    line-height: 35px;
  }
}
@media(max-height: 642px){
  .header{
    margin-bottom: 25px;
    font-size: 10px;
  }
  .header__heading{
    line-height: 20px;
  }
}
.customization{
  margin-bottom: 30px;
}
.customization__block{
  position: relative;
  padding-top: 20px;
}
.customization__block:before,
.customization__block:after{
  position: absolute;
  top: 0;
  content: '';
  width:calc(50% - 40px);
  height: 4px;
  display: block;
  background: #000;
}
.customization__block:before{
  left: 0;
}
.customization__block:after{
  right: 0;
}
.customization__form{
  position: relative;
  max-width: 700px;
  margin: 0 auto;
  display: none;
  transition: 1s;
  height: 200px;
  overflow-y: auto;
}
.customizer_btn{
  font-size: 20px;
  width: 60px;
  height: 30px;
  border: none;
  background: #000;
  color: #fff;
  transition: 1s;
  position: absolute;
  top: -15px;
  left: 50%;
  transform: translateX(-50%);
  cursor: pointer;
  border-radius: 0;
}
.customizer_btn:hover{
  background: #fff;
  color: #000;
  outline: 1px solid #000;
}
.customizer_btn:active{
  top: -5px;
}
.reset_btn{
  position: relative;
  font-size: 20px;
  background: #000;
  color: #fff;
  transition: 1s;
  cursor: pointer;
  border: none;
  border-radius: 0;
  padding: 10px;
  margin: 0 0 10px 10px;
}
.reset_btn:hover{
  background: #fff;
  color: #000;
  outline: 1px solid #000;
}
.reset_btn:active{
  top: -5px;
}
.customization__form_item{
  padding: 5px;
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin: 10px 0;
}
.customization__form_inputs{
  flex: 0 0 40%;
  display: flex;
  justify-content: flex-end;
}
.banner_sizes span{
  line-height: 28px;
  font-weight: 700;
}
.customization__form_text{
  margin: 0 10px;
}
.customization__block input{
  border: 1px solid black;
  padding-left: 5px;
}
.customization__block input[type='color']{
  border: none;
  background: none;
  outline: none;
  transform: scale(1.2);
}

@media(max-height: 960px){
  .customization__form{
    position: absolute;
    width: 100%;
    top: 20px;
    display: none;
    left: 50%;
    z-index: 999;
    transform: translateX(-50%);
    background: rgb(255, 255, 255);
    outline: rgb(0, 0, 0) solid 1px;
  }
}
@media(max-height: 642px){
  .customization{
    margin-bottom: 10px;
  }
}
@media(max-width: 900px){
  .customizer_btn:hover{
    background: #000;
    color: #fff;
    outline: none;
  }
  .reset_btn:hover{
    background: #000;
    color: #fff;
    outline: none;
  }
}
@media(max-width: 700px){
  .customization__form{
    width: 100%;
    height: auto;
    font-size: 14px;
  }
  .customization__block input{
    width: 100%;
  }
}
#banner_img:hover{
  cursor: move;
}
#banner_text:hover{
  cursor: move;
  outline: 1px solid #fff;
}
.banner{
  left: 50%;
  transform: translateX(-50%);
}
.preview{
  margin-bottom: 30px;
}
.export{
  text-align: center;
  margin-bottom: 30px;
  position: relative;
  display: flex;
  justify-content: center;
}
.export__btn{
  position: relative;
  padding: 20px;
  background: #000;
  border: none;
  color: #fff;
  margin: 0 15px;
  font-size: 20px;
  letter-spacing: 1px;
  transition: 1s;
  cursor: pointer;
}
.export__btn:hover{
  color: #000;
  background: #fff;
  outline: 1px solid #000;
}
.export__btn:active{
  top: 10px;
}
.notification{
  opacity: .8;
  position: absolute;
  top: 100px;
  left: 50%;
  transform: translateX(-50%);
  background: black;
  display: none;
  color:rgba(255,255,255,1);
  padding: 10px;
  border-radius: 10px;
}
@media(max-width: 900px){
  .export__btn:hover{
    color:#fff;
    background: #000;
    outline: none;
  }
}
@media(max-width: 700px){
  .export__btn{
    padding: 10px;
    margin: 0 10px;
    font-size: 14px;
    letter-spacing: 0px;
  }
}
.container{
  max-width: 1000px;
  margin: 0 auto;
  padding: 10px;
  /* height: 100vh; */
}

@media(max-width: 1200px){
  .container{
    max-width: 800px;
  }
}
@media(max-width: 900px){
  .container{
    max-width: 600px;
  }
}
@media(max-width: 700px){
  .container{
    max-width: 400px;
  }
}
body{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
  overflow-y: overlay;

  
}
div{
  box-sizing: border-box;
}
h1,h2,h3,h4,h5,h6{
  margin: 0;
}
ul{
  padding: 0;
}
